
import { defineComponent } from 'vue';
import { useIconColor } from '@/composables/iconColor';

export default defineComponent({
	name: 'WhatsAppIcon',
	props: {
		color: {
			type: String,
			default: 'gray',
		},
		width: {
			type: String,
			default: '19',
		},
		height: {
			type: String,
			default: '19',
		},
	},
	setup(props) {
		const colorClass = useIconColor(props.color);

		return {
			colorClass,
		};
	},
});
